import { Clue } from "./clue";
import { Row, RowState } from "./Row";
import { maxGuesses } from "./util";

export function About(props: { toaq: boolean }) {
  return (
    <div className="App-about">
      {props.toaq ? (
        <>
          <p>
            <i>m Toaly</i> b beokop z m Wordle na ru no z Taqzu.
          </p>
          <p>
            Leo sho jedua h sa shu toa ch jto h cho sa bu huaq pu{" "}
            {maxGuesses} da.
            <br />
            Heq sh toa sa o'aomo (') da.
            <br />
            Cua joqkea tsh jto h cy s tua reoma ja ke la da.
          </p>
        </>
      ) : (
        <>
          <p>
            <i>m Toaly</i> is a Wordle clone for{" "}
            <a href="https://toaq.me">Toaq</a>, a constructed language.
          </p>
          <p>
            You get {maxGuesses} tries to guess a target word.
            <br />
            There is never an apostrophe (') in the target word.
            <br />
            After each guess, you get Mastermind-style feedback.
          </p>
        </>
      )}
      <hr />
      <Row
        rowState={RowState.LockedIn}
        wordLength={4}
        cluedLetters={[
          { clue: Clue.Absent, letter: "t" },
          { clue: Clue.Absent, letter: "o" },
          { clue: Clue.Correct, letter: "a" },
          { clue: Clue.Elsewhere, letter: "" },
        ]}
      />
      {props.toaq ? (
        <>
          <p>
            Heq sh toa sa <b>tteq</b> ra <b>oq'ogu</b> da.
          </p>
          <p>
            Due ke <b className={"green-bg"}>aq'an</b> da!{" "}
            <b className={"green-bg"}>Aq'an</b> saqko la da.
            <br />
            <strong>(Da heq sh toa sa meaheo aq'an da.)</strong>
          </p>
          <p>
            Ne sa <b className={"yellow-bg"}>q'rue</b> sh toa <em>t sa meaheo</em> da.
            <br />
            <strong>(Da ne sa pu mq da. )</strong>
          </p>
          <hr />
          <p>
            Tu ttua h ke <b className={"yellow-bg"}>q'irue</b> sa meaheo ba:
          </p>
        </>
      ) : (
        <>
          <p>
            <b>T</b> and <b>O</b> aren't in the target word at all.
          </p>
          <p>
            <b className={"green-bg"}>A</b> is correct! The third letter is{" "}
            <b className={"green-bg"}>A</b>
            .<br />
            <strong>(There may still be a second A in the word.)</strong>
          </p>
          <p>
            <b className={"yellow-bg"}>I</b> occurs <em>elsewhere</em> in the
            target word.
            <br />
            <strong>(Perhaps more than once. )</strong>
          </p>
          <hr />
          <p>
            Let's move the <b>I</b> in our next guess:
          </p>
        </>
      )}
      <Row
        rowState={RowState.LockedIn}
        wordLength={4}
        cluedLetters={[
          { clue: Clue.Absent, letter: "k" },
          { clue: Clue.Correct, letter: "" },
          { clue: Clue.Correct, letter: "a" },
          { clue: Clue.Correct, letter: "q" },
        ]}
        annotation={props.toaq ? "Ju da!" : "So close!"}
      />
      <Row
        rowState={RowState.LockedIn}
        wordLength={4}
        cluedLetters={[
          { clue: Clue.Correct, letter: "j" },
          { clue: Clue.Correct, letter: "" },
          { clue: Clue.Correct, letter: "a" },
          { clue: Clue.Correct, letter: "q" },
        ]}
        annotation={props.toaq ? "Aja!" : "Got it!"}
      />
    </div>
  );
}
